<template>
  <!-- pages/dj/dj.wxml -->
  <div :class="['lottery', showPrize ? 'no-scrol' : '']">
    <!-- 广播 中奖用户 -->
    <div class="ad-zhongjiang">
      恭喜用户&nbsp;<b>西亚</b>&nbsp;180****0122用户获得&nbsp;<b>88元</b>&nbsp;现金
    </div>
    <!-- 转盘 -->
    <div class="turn-table">
      <!-- 走马灯 -->
      <div class="zou-ma-deng">
        <div class="yellow-deng"></div>
        <div class="white-deng"></div>
        <div class="yellow-deng"></div>
        <div class="white-deng"></div>
        <div class="yellow-deng"></div>
        <div class="white-deng"></div>
        <div class="yellow-deng"></div>
        <div class="white-deng"></div>
        <div class="yellow-deng"></div>
        <div class="white-deng"></div>
        <div class="yellow-deng"></div>
        <div class="white-deng"></div>
      </div>

      <!-- 外边框 -->
      <div class="bian-kuang"></div>
      <!-- 抽奖内容 -->
      <div
        class="prize-list"
        :style="
          'transition: transform ' +
          time +
          's ease 0s;transform: rotate(' +
          deg +
          'deg);'
        "
      ></div>
      <!-- 指针 -->
      <div class="pointer" @click="run"></div>
    </div>
    <!-- 兔子 -->
    <div class="rabbit"></div>
    <!-- 活动规则 按钮 -->
    <div class="rule-button" @click="showRule = true"></div>
    <!-- 点击抽奖的按钮 -->
    <div @click="run" class="choujiang-button"></div>
    <!-- 规则弹窗 -->
    <div v-show="showRule" class="zhezhaoceng">
      <rule-frame @close="close"></rule-frame>
    </div>
    <!-- 奖品弹窗 -->
    <div v-show="showPrize" class="zhezhaoceng">
      <prize-frame @close="close"></prize-frame>
    </div>
  </div>
</template>

<script>
import PrizeFrame from "../components/PrizeFrame";
import RuleFrame from "../components/RuleFrame";
export default {
  name: "my-lottery",
  components: {
    PrizeFrame,
    RuleFrame,
  },
  data() {
    return {
      showPrize: false, // 奖品弹窗是否打开
      showRule: false, // 规则介绍弹窗
      flag: false, // 判断是否点击
      time: 1.8, // 转盘滚动的时间,单位(s)
      deg: 0, // 转盘转动的角度
    };
  },
  created() {},
  methods: {
    // 获取抽奖几率
    // 抽奖
    run() {
      if (this.flag) {
        return;
      }
      this.flag = true;
      // console.log("run");
      this.deg += 900;
      setTimeout(() => {
        this.flag = false;
        // 遍历奖品
        if (this.index === 3) {
          this.index = 1;
        } else {
          this.index += 1;
        }
        // 打开奖品弹窗
        this.showPrize = true;
      }, this.time * 1000);
    },
    // 关闭弹窗
    close() {
      // console.log("close");
      this.showPrize = false;
      this.showRule = false;
    },
  },
};
</script>

<style lang="scss" scoped>
