    no-repeat 0 0 / 750rem 1556rem;
  height: 1556rem;
  position: relative;

  .ad-zhongjiang {
    padding-left: 92rem;
    position: absolute;
    line-height: 92rem;
    font-size: 25rem;
    color: #24765d;
    top: 434rem;
    width: 624rem;
    height: 72rem;
    background: url(https://img01.yzcdn.cn/upload_files/2021/03/26/FrbyDBry1KgWwUyl1UmcHzqvDxNB.png)
      no-repeat 0 0/624rem 72rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .choujiang-button {
    position: absolute;
    top: 1335rem;
    width: 404rem;
    height: 129rem;
  }

  .rabbit {
    position: absolute;
    z-index: 3;
    top: 1003rem;
    left: 23rem;
