    height: 299rem;
    background: url("https://img01.yzcdn.cn/upload_files/2021/03/26/FsvAlzlAqtqnzDvg2i5Qq9HW67zw.png")
      0 0 /155rem 299rem no-repeat;
  }

  .turn-table {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 535rem;
    width: 710rem;
    height: 710rem;
    font-size: calc(1rem * 710 / 369); // 提供的图片和设计图居然大小不一样,服了
    // background: white;
    .zou-ma-deng {
      position: absolute;
      width: 100%;
      height: 100%;
      // border: 1px solid black;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      div {
        width: 84rem;
        height: 84rem;
        position: absolute;
        z-index: 6;
      }
      div:nth-of-type(1) {
        transform: rotate(0deg) translateX(313rem);
      }
      div:nth-of-type(2) {
        transform: rotate(30deg) translateX(313rem);
      }
      div:nth-of-type(3) {
        transform: rotate(60deg) translateX(313rem);
      }
      div:nth-of-type(4) {
        transform: rotate(90deg) translateX(313rem);
      }
      div:nth-of-type(5) {
        transform: rotate(120deg) translateX(313rem);
      }
      div:nth-of-type(6) {
        transform: rotate(150deg) translateX(313rem);
      }
      div:nth-of-type(7) {
        transform: rotate(180deg) translateX(313rem);
      }
      div:nth-of-type(8) {
        transform: rotate(210deg) translateX(313rem);
      }
      div:nth-of-type(9) {
        transform: rotate(240deg) translateX(313rem);
      }
      div:nth-of-type(10) {
        transform: rotate(270deg) translateX(313rem);
