      div:nth-of-type(11) {
        transform: rotate(300deg) translateX(313rem);
      }
      div:nth-of-type(12) {
        transform: rotate(330deg) translateX(313rem);
      }

      .yellow-deng {
        animation: trun-color 1s linear 0s infinite;
      }
      .white-deng {
        animation: trun-color 1s linear 0.5s infinite;
      }

      // 走马灯动画
      @keyframes trun-color {
        0% {
          background: url("https://img01.yzcdn.cn/upload_files/2021/03/30/Fkj1nSD_TB_ARA05fpNpyFNmMnqt.png")
            0 0 / 84rem 84rem no-repeat;
        }
        // 设置 49% 50% 防止有过渡动画
        49% {
          background: url("https://img01.yzcdn.cn/upload_files/2021/03/30/Fkj1nSD_TB_ARA05fpNpyFNmMnqt.png")
            0 0 / 84rem 84rem no-repeat;
        }
        50% {
          background: url("https://img01.yzcdn.cn/upload_files/2021/03/30/FjdJpjsKRVh1JKvt_buQewKziXsI.png")
            0 0 / 84rem 84rem no-repeat;
        }
        100% {
          background: url("https://img01.yzcdn.cn/upload_files/2021/03/30/FjdJpjsKRVh1JKvt_buQewKziXsI.png")
            0 0 / 84rem 84rem no-repeat;
        }
      }
    }

    .bian-kuang {
      position: absolute;
      top: 2em;
      // 由于切的图片不是中心对称所以不能通过绝对剧中的方式定位元素
      width: 369em;
      height: 369em;
      background: url("https://img01.yzcdn.cn/upload_files/2021/03/26/FuYlolM8H5SOdp7nS1i1JFpZMc7E.png")
        0 0 / 369em 369em no-repeat;
    }
    .prize-list {
      position: absolute;
      width: 300em;
      height: 300em;
      // width: 276em;
      // height: 276em;
      // 由于背景要和边框紧贴着,所以 宽高 比实际长,这样就没有空隙
      // border: 1px solid black;
      border-radius: 50%;
      background: white
        url("https://img01.yzcdn.cn/upload_files/2021/03/26/FlC_u3Q_hakpJkRWhrK8qkYXInUa.png")
        center center / 276em 276em no-repeat;
    }
    .pointer {
      transform: translateX(2em); // 不知道为什么就是对不齐
      position: absolute;
      width: 111em;
      height: 120em;
      background: url("https://img01.yzcdn.cn/upload_files/2021/03/26/FnaUF30sD8ofAEfTPIdrRdRrjG6c.png")
        0 0 / 111em 120em no-repeat;
    }
  }

  .rule-button {
    position: absolute;
    top: 241rem;
    right: 0;
    width: 45rem;
    height: 152rem;
    // border: 1px solid black;
  }
}

.zhezhaoceng {
  position: absolute;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
</style>
