<script>
export default {
  name: "my-lottery",
  data() {
    return {
      showPrize: false, // 奖品弹窗是否打开
      showRule: false, // 规则介绍弹窗
      index: 0,
      flag: false, // 判断是否点击
      time: 1.8, // 转盘滚动的时间,单位(s)
      deg: 0, // 转盘转动的角度
    };
  },
  created() {
  },
  methods: {
    // 获取抽奖几率
    // 抽奖
    run() {
      if (this.flag) {
        return;
      }
      this.flag = true;
      // console.log("run");
      this.deg += 900;
      setTimeout(() => {
        this.flag = false;
        // 遍历奖品
        if (this.index === 3) {
          this.index = 1;
        } else {
          this.index += 1;
        }
        // 打开奖品弹窗
        this.showPrize = true;
      }, this.time * 1000);
    },
    // 关闭弹窗
    close() {
      // console.log("close");
      this.showPrize = false;
      this.showRule = false;
    },
  },
};
</script>

