import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/Index.vue'
import Index2 from '../views/Index2.vue'
import Index3 from '../views/Index3.vue'
import Invite from '../views/Invite.vue'
import Invite2 from '../views/Invite2.vue'
import Invite3 from '../views/Invite3.vue'
import MyLottery from '../views/MyLottery.vue'
import MyLottery2 from '../views/MyLottery2.vue'
import MyLottery3 from '../views/MyLottery3.vue'
import Record from '../views/Record.vue'
import test from '../views/test.vue'


const routes = [
  { path: '/', redirect: '/index' },
  { path: '/index', component: Index },
  { path: '/index2', component: Index2 },
  { path: '/index3', component: Index3 },
  { path: '/lottery', component: MyLottery },
  { path: '/lottery2', component: MyLottery2 },
  { path: '/lottery3', component: MyLottery3 },
  { path: '/invite', component: Invite },
  { path: '/invite2', component: Invite2 },
  { path: '/invite3', component: Invite3 },
  { path: '/record', component: Record },
  { path: '/test', component: test },
]

const router = new VueRouter({
  routes
})

Vue.use(VueRouter)

export default router
