<template>
  <div :class="['invite', isShowRule ? 'isRuleFrame' : '']">
    <!-- 活动规则 -->
    <div class="zhezhaoceng" v-if="isShowRule">
      <rule-frame></rule-frame>
    </div>
    <!-- 邀请好友区域 -->
    <div class="main">
      <!-- 卡片区域 -->
      <div class="card">
        <div class="title">
          <span>邀请 <b>5</b> 名好友即可再获得一次抽奖机会</span>
        </div>
        <!-- 刺激邀请 -->
        <div class="ti-shi" v-if="helpNum === 0">
          <span>邀请越多机会越多</span>
        </div>
        <!-- 抽奖 -->
        <div class="chou-jiang" v-else>
          <span>助力已满{{ helpNum }}人，共获得{{ frequency }}次抽奖机会</span>
          <div class="chou-jiang-btn" @click="goLottery">
            <span>去抽奖</span>
          </div>
        </div>
        <!-- 头像区 -->
        <div class="tou-xiang-qu">
          <div class="item" v-for="v in fiveHlep" :key="v.id">
            <div @click="inviteFrined">
              <!-- 点击已邀请的头像，停止冒泡 -->
              <img :src="v.img" alt="" v-if="v.img" @click.stop="" />
            </div>
            <span>{{ v.name ? v.name : "待邀请" }}</span>
          </div>
        </div>
        <!-- 邀请好友 -->
        <div class="btn-friend" @click="inviteFrined">
          <span>邀请好友</span>
          <!-- 再来一次 -->
          <div class="again-more" v-if="helpNum !== 0">
            <span>再来一次，冲击大奖！</span>
          </div>
        </div>
        <!-- 倒计时 -->
        <div class="count-down">
          <div class="ji-suan">
            <span>倒计时</span>
          </div>
          <div class="time">{{ time.h }}</div>
          <div class="mao-hao">:</div>
          <div class="time">{{ time.m }}</div>
          <div class="mao-hao">:</div>
          <div class="time">{{ time.s }}</div>
        </div>
      </div>
      <!-- 助力区域 -->
      <div class="interlayer" v-if="helpNum !== 0">
        <span>
          共有
          <b>{{ helpNum }}</b>
          位好友帮你助力
        </span>
      </div>
    </div>
    <!-- 底部区域 -->
    <div class="foot">
      <!-- 助力好友列表 -->
      <div class="firend-list">
        <div v-for="v in friendlist" :key="v.id">
          <img :src="v.img" />
          <div class="item">
            <!-- 名字 -->
            <div class="name">
              <span>{{ v.name }}</span>
            </div>
            <!-- 日期 -->
            <div class="date">
              <span>{{ v.date }}</span>
            </div>
            <!-- 已助力 -->
            <div class="helped"><span>已助力</span></div>
          </div>
        </div>
      </div>
      <!-- 关注按钮 -->
      <div class="btnFollow">
        <span>点击关注<b>"植选植物奶"</b>微信公众号</span>
