  },
  methods: {
    // 邀请好友
    inviteFrined() {
      console.log(123);
    },
    // 倒计时逻辑
    countDown() {
      setInterval(() => {
        if (this.time.s === 0) {
          if (this.time.m === 0) {
            if (this.time.h === 0) {
              return;
            } else {
              this.time.h = this.time.h - 1;
              this.time.m = 59;
            }
          } else {
            this.time.m = this.time.m - 1;
            this.time.s = 59;
          }
        } else {
          this.time.s = this.time.s - 1;
        }
      }, 1000);
    },
    // 去抽奖
    goLottery() {
      this.$router.push("/lottery");
    },
  },
  created() {
    this.countDown();
  },
  computed: {
    // 主力次数
    helpNum() {
      return this.friendlist.length;
    },
    // 抽奖次数
    frequency() {
      return parseInt(this.helpNum / 5);
    },
    // 5个助力满足情况
    fiveHlep() {
      // let result = [];
      // let end = this.frequency * 5 + 5 - this.helpNum;
      // if (end === 5) {
      //   result = [{}, {}, {}, {}, {}];
      // } else {
      //   result = this.friendlist.slice(this.frequency * 5);
      //   for (let ii = 0; ii < end; ii++) {
      //     result.push({});
      //   }
      // }
      // return result;
      let result = this.friendlist.slice(-5);
      while (1) {
        if (result.length < 5) {
          // 不满5人添加空对象
          result.push({});
        } else {
          return result;
        }
      }
    },
  },
};
</script>

